import { Sidebar } from "grommet";
import { VerticalMenu } from "grommet-controls";

export const SideBar = () => {
  const pathname = window.location.pathname;
  const partsPath = pathname?.split('/');
  const lastIndex = partsPath.length - 1;
  const selectedId = partsPath[lastIndex] || 'what-is';

  return (<Sidebar
    background='light-2'
    elevation='small'
    gridArea="sidebar"
  >
    <VerticalMenu
      activeItem={{ id: selectedId }}
      expandAll={true}
      items={
        [
          {
            id: 'what-is',
            label: 'What is Fuzzy Searcher?',
            href: `${process.env.PUBLIC_URL}/what-is`
          },
          {
            id: 'started',
            label: 'Getting Started',
            items: [
              {
                id: 'installation',
                label: 'Installation',
                href: `${process.env.PUBLIC_URL}/installation`
              },
              {
                id: 'usage',
                label: 'Usage',
                href: `${process.env.PUBLIC_URL}/usage`
              }
            ]
          },
          {
            id: 'live-demo',
            label: 'Live Demo',
            href: `${process.env.PUBLIC_URL}/live-demo`
          },
          {
            id: 'contribute',
            label: 'Contribute',
            items: [
              {
                id: 'loaders',
                label: 'Loaders',
                href: `${process.env.PUBLIC_URL}/loaders`
              }
            ]
          },
        ]
      }
    />
  </Sidebar>);
}