import React, { useEffect } from "react";
import styled from 'styled-components';
import 'highlight.js/styles/tomorrow-night-bright.css';
import hljs from 'highlight.js';
import javascript from 'highlight.js/lib/languages/javascript';
import { Heading, Markdown, Page, PageContent } from "grommet";


export const Usage = () => {
  const delareAnalyzer = "const analyzer = new Analyzer();\n";
  const distanceVariable = "const distance = 1;\n"
  const loadAnalyzer = "analyzer.load(['KE','KEY','KA','KO','PO','LA']);\n";
  const searchKey = "const matches = analyzer.search('KEY', distance);\n";
  const expectedResult = "matches.join(',') //--> KO,KA,KE,KEY";
  const basicExample = delareAnalyzer + distanceVariable + loadAnalyzer + searchKey + expectedResult;
  const StyledP = styled.p`
    max-width: max-content;
`;
  const content = `In the example we are initializing the **Analizer** class with the default values, default **Loaders**.
  Then we are setting the distance as 1, this is related with the [Levenshtein Distance](https://en.wikipedia.org/wiki/Levenshtein_distance), for conducting the search.

  Because we are using the default loader, we are loading our analyzer with a list of words, these words are going to be converted in a [Trie](https://en.wikipedia.org/wiki/Trie) for rapid access for future searches.
  
  The next line is conducting the search with a key (**KEY**) over the previous generated **Trie** with a distance a **levenshtein distance** of 1.
  In the last line we should see the exact same list of words as in the example.
  `;
  useEffect(() => {
    hljs.registerLanguage('js', javascript);
    hljs.initHighlighting();
  }, []);

  return (
    <Page kind="full">
      <Heading alignSelf='center' level={2}>Usage</Heading>
      <PageContent>
        <Heading level={3}>Basic Example</Heading>
        <pre><code className="language-js">
          {basicExample}
        </code></pre>
        <Markdown components={{ p: StyledP }}>
          {content}
        </Markdown>
      </PageContent>
    </Page>
  );
}