import React, { useEffect } from 'react';
import 'highlight.js/styles/tomorrow-night-bright.css';
import hljs from 'highlight.js';
import typescript from 'highlight.js/lib/languages/typescript';
import styled from 'styled-components';
import {
  Page,
  PageContent,
  Paragraph,
  Heading,
  Markdown,
} from 'grommet'

export const Loaders = () => {
  const StyledP = styled.p`
    max-width: max-content;
  `;

  const whatIsLoader = `A **loader** is a class that implements the interface *Loader* and along side with 2 methods:`;
  const defaultLoader = `The default *loader* is the **ListLoader** class:`;
  const buildLoader = `We encorage you to build your own loaders for your special requirements or adaptations, meanwhile we continue expanding this **loader library** and add every loader that the comunity propose.`;
  const testLoader = `The **Analyzer** class constructor takes an array of **loaders** as input parameter, so you basically must instantiate this class with your **custom loader** implementations.`;

  const listLoaderCode = `
  export class ListLoader implements Loader {
    canResolve(arg: any): boolean {
      return Array.isArray(arg) && arg.every((elem) => isString(elem));
    }
  
    load(arg: any): string[] {
      return arg as string[];
    }
  }
  `;

  const loaderCode = `
  export interface Loader {
    canResolve: (arg: any) => boolean;
    load: (arg: any) => string[];
  }  
  `;

  const implementCustomLoader = `
  export class CustomLoader implements Loader {
    canResolve(arg: any): boolean {
      return true;
    }
  
    load(arg: any): string[] {
      return arg as string[];
    }
  }

  const analizer = new Analyzer([new CustomLoader()]);
  `;

  useEffect(() => {
    hljs.registerLanguage('typescript', typescript);
    hljs.initHighlighting();
  }, []);
  
  return (
    <Page kind="full">
      <Heading alignSelf='center' level={2}>Loaders</Heading>
      <PageContent>
        <Heading level={3}>What is a Loader?</Heading>
        <Markdown components={{ p: StyledP }}>
          {whatIsLoader}
        </Markdown>
        <pre><code className='language-typescript'>{loaderCode}</code></pre>
        <Markdown components={{ p: StyledP }}>
          {defaultLoader}
        </Markdown>
        <pre><code className='language-typescript'>{listLoaderCode}</code></pre>
        <Markdown components={{ p: StyledP }}>
          {buildLoader}
        </Markdown>
        <Heading level={3}>How do I test my custom loader?</Heading>
        <Markdown components={{ p: StyledP }}>
          {testLoader}
        </Markdown>
        <pre><code className='language-typescript'>{implementCustomLoader}</code></pre>
          <Paragraph>
            <br />
          </Paragraph>
      </PageContent>
    </Page>);
}
