import React, { useEffect, useMemo } from "react";
import { Analyzer } from "fuzzy-searcher";
import { Box, Button, Form, FormField, Heading, List, Page, PageContent, Table, TableBody, TableCell, TableHeader, TableRow, TextInput } from "grommet";
import { FormTrash } from "grommet-icons";


export const LiveDemo = () => {
  const analyzer = useMemo(() => new Analyzer(), []);
  const [word, setWord] = React.useState<{ name?: string}>({});
  const [list, setList] = React.useState<string[]>([]);
  const [key, setKey] = React.useState('');
  const [matches, setMatches] = React.useState<string[]>([]);
  const removeWord = (index: number) => setList([...list.filter((_, idx) => idx !== index)]);
  const disableSubmit = !word?.name || list.includes(word.name!)

  useEffect(() => {
    analyzer.load(list);
    setMatches(analyzer.search(key, 1));
  }, [list, key, analyzer]);

  return (
    <Page kind="narrow">
      <Heading alignSelf='center' level={2}>Examples</Heading>
      <PageContent alignContent="center" gap="large">
        <Form
          value={word}
          onChange={newValue => setWord(newValue)}
          onSubmit={({ value }: any) => setList([...list, value.name])}
          >
          <FormField name="name" htmlFor="text-input-id" label="Words">
            <TextInput id="text-input-id" name="name" />
          </FormField>
          <Box direction="row">
            <Button type="submit" primary label="Submit" disabled={disableSubmit} />
          </Box>
        </Form>
        <Table>
          <TableHeader>
            <TableRow>
              <TableCell scope="col" border="bottom">
                Word
              </TableCell>
              <TableCell scope="col" border="bottom">
                Delete
              </TableCell>
            </TableRow>
          </TableHeader>
          <TableBody>
            {list.map((value, index) => (
              <TableRow key={index}>
                <TableCell scope="row">
                  <strong>{value}</strong>
                </TableCell>
                <TableCell><Button primary onClick={() => removeWord(index)} icon={<FormTrash />}></Button></TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>

        <FormField label="Key">
          <TextInput
            placeholder="type here key to search"
            value={key}
            onChange={event => setKey(event.target.value)}
          />
        </FormField>
        <Box>
          <Heading alignSelf='center' level={3}>Results</Heading>
          <List
            data={matches}
          />
        </Box>
      </PageContent>
    </Page>
  );
}