import { Heading, Page, PageContent, Paragraph } from "grommet";
import 'highlight.js/styles/tomorrow-night-bright.css';
import hljs from 'highlight.js';
import bash from 'highlight.js/lib/languages/bash';
import javascript from 'highlight.js/lib/languages/javascript';
import { useEffect } from "react";
hljs.registerLanguage('sh', bash);
hljs.registerLanguage('js', javascript);


export const Installation = () => {
  const esImport = "import { Analyzer } from 'fuzzy-searcher';"
  const commonJS = "const { Analyzer } = require('fuzzy-searcher');";
  useEffect(() => {
    hljs.initHighlighting();
  }, []);

  return (
    <Page kind="full">
      <Heading alignSelf='center' level={2}>Installation</Heading>
      <PageContent>
        <Paragraph fill>
          <Heading alignSelf='center' level={3}>NPM</Heading>
          <pre><code className="language-sh">
            $ npm install --save fuzzy-searcher
          </code></pre>
          <Heading alignSelf='center' level={3}>Yarn</Heading>
          <pre><code className="language-sh">
            $ yarn add fuzzy-searcher
          </code></pre>

          <Heading alignSelf='center' level={4}>Importing</Heading>
          ES6 module syntax:
          <pre><code className="language-js">
            {esImport}
          </code></pre>
          CommonJS:
          <pre><code className="language-js">
            {commonJS}
          </code></pre>
        </Paragraph>
      </PageContent>
    </Page>);
}