import React from 'react';
import {
  Page,
  PageContent,
  Paragraph,
  Heading,
  Markdown,
} from 'grommet'

export const WhatIs = () => {
  return (<Page kind="full">
    <Heading alignSelf='center' level={2}>What is Fuzzy Searcher?</Heading>
    <PageContent>
      <Paragraph alignSelf='center'>
        <Markdown>
          **fuzzy-searcher** is a lightweight fuzzy-search library, developed for be used on [Node.js](https://nodejs.org/es/) as in different Frontend frameworks (React, Angular, etc), with zero dependencies.
        </Markdown>
      </Paragraph>
      <Heading level={3}>What is fuzzy searching?</Heading>
      <Paragraph fill>
        Generally speaking, fuzzy searching (more formally known as approximate string matching) is the technique of finding strings that are approximately equal to a given pattern (rather than exactly).
      </Paragraph>
      <Heading level={3}>How does it work under the hood?</Heading>
      <Markdown>It is implemented using a [Trie](https://en.wikipedia.org/wiki/Trie) for loading all the different words in the space search.
        It uses an structure of different **Loaders** for being able to abstract the loading of the words in different formats in an extensible manner.
        Then a [Levenshtein automaton](https://en.wikipedia.org/wiki/Levenshtein_automaton) is implemented for conducting the search.
        Some optimizations where implemented according to this article from [Jules Jacob](https://julesjacobs.com/2016/08/26/simpler-and-faster-levenshtein-automata.html)
      </Markdown>
    </PageContent>
  </Page>);
}
